import React from 'react'
import { login } from '../../store';

const SubmitForm = dispatch => {
  // const value1 = document.getElementById('signup-username').value
  // const value2 = document.getElementById('signup-email').value
  // const value3 = document.getElementById('signup-password').value
  // const value4 = document.getElementById('signup-password-confirmation').value

  dispatch(login())
}

export default function UserForm({trigger}) {
  return (
    <div>
      <form>
        <div>
          <input id="signup-username"/>
        </div>
        <div>
          <input id="signup-email"/>
        </div>
        <div>
          <input id="signup-password" type="password"/>
        </div>
        <div>
          <input id="signup-password-confirmation" type="password"/>
        </div>
        <div>
          <input type="submit" onClick={e => {
            e.preventDefault();
            SubmitForm(trigger);
          }}/>
        </div>
      </form>
    </div>
  )
}
